.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

/* flex-start ä¸ºé»˜è®¤ å¯ä»¥ä¸å†™ */

.gpt3__navbar-links_logo {
    margin-right: 2rem;

}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #ffff;
    font-family: var(--font-family);
    /* ziti ç²—ç»†å­—ä½“ï¼Œå±æ€§ç»“å°¾è¦ç”¨åˆ†å·*/
