    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;

    /* cursor:é¼ æ ‡ç§»åŠ¨åˆ°å†…å®¹æ—¶ï¼Œæ˜¾ç¤ºçš„æ ·å¼ï¼Œçˆªçˆª */

    /* ä¸Šé¢çš„å±æ€§ï¼Œä»inspection ç½‘é¡µæ˜¾ç¤ºï¼Œç½‘ç«™è®¾è®¡å›¾ä¸­ç›´æ¥æ’åˆ°CSSä»£ç ï¼Œä¸éœ€è¦ğŸ‘‹å†™äº†ï¼Œæ‹¿æ¥å³ç”¨ */


}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;

    line-height: 25px;
    font-size: 18px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;

}

.gpt3__navbar-menu svg {
    cursor: pointer;
}


/* äº¤å‰è½´çš„æ˜¾ç¤º */
.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: felx-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);

}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }

    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }

}

@media screen and (max-width: 550px) {
    .gpt3__navbar {
        padding: 2rem;
    }

    .gpt3__navbar_sign {
        display: none;
    }

    .gpt3__navbar-menu_container {
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}